import IndentedList from '../components/DataRuns/FeatureMatrixView/IndentedList';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import {changeMatrixRowFilters} from '../actions';
//import { decrement, increment } from '../actions';
import { StoreState } from '../types';


//  reducer  props 
const mapStateToProps = (state: StoreState) => ({
    MatrixRowFilters: state.matrixRowFilters
})

//  action  props 
const mapDispatchToProps = (dispatch: Dispatch) => ({
    changeMatrixRowFilters: (MatrixRowFilters:any) => dispatch(changeMatrixRowFilters(MatrixRowFilters))
})

//  connect  Counter 
export default connect(mapStateToProps, mapDispatchToProps)(IndentedList);
