import ParallelSets from '../components/DataRuns/PSView/ParallelSets';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import {changeFilters} from '../actions';
//import { decrement, increment } from '../actions';
import { StoreState } from '../types';


//  reducer  props 
const mapStateToProps = (state: StoreState) => ({
    PSDimensions: state.PSDimensions
})

//  action  props 
const mapDispatchToProps = (dispatch: Dispatch) => ({
    changeFilters: (filters:any) => dispatch(changeFilters(filters))
})

//  connect  Counter 
export default connect(mapStateToProps, mapDispatchToProps)(ParallelSets);
